<template>
    <div>
        <ConsoleHeader />
      
        <div class="content-wrapper">
            <!-- Content Header (Page header) -->
            <div class="content-header">
                <div class="container-fluid">
                    <div class="row mb-2">
                    <div class="col-sm-6 text-start">
                        <h1 class="m-0 text-dark">Settings<sup><a href="javascript:void(0)" class="text-dark" @click.prevent="newContentView" title="Create"><i class="mdi mdi-plus-circle"></i></a></sup></h1>
                    </div><!-- /.col -->
                    <div class="col-sm-6">
                        <ol class="breadcrumb float-sm-right">
                            <li class="breadcrumb-item"><router-link to="/console">Home</router-link></li>
                            <li class="breadcrumb-item active">Settings</li>
                        </ol>
                    </div><!-- /.col -->
                    </div><!-- /.row -->
                </div><!-- /.container-fluid -->
            </div>
            <!-- /.content-header -->

            <!-- Main content -->
            <section class="content">

                <div class="container-fluid" >
                    
                
                    <div class="pb-5" v-if="NewContentView === false">
                        
                        <div v-if="contents.length < 1" class="text-start">
                            <b>No Contents added yet!</b>
                        </div>

                        <div class="row no-gutters pb-5" v-else >
                            <div class="col-xl-3 col-sm-6 col-6 h-100 pr-2" v-for="content in contents" v-bind:key="content.id">
                                <!-- small box -->
                                <div class="small-box bg-dark h-100 pt-4">
                                    <div class="inner">
                                        <div v-if="content.banner === null">
                                            <img src="@/assets/images/image.png" class="img-fluid" style="width: 8em;" />
                                        </div>
                                        <div v-else >
                                            <img :src="content.banner" class="img-fluid" style="width: 7em; height: 7em; object-fit: contain;" />
                                        </div>
                                        <!-- <i class="mdi mdi-plus-circle" v-if="content.image === null"></i> -->
                                        <h5 class="text-truncate" :title="content.title">{{content.title}}</h5>
                                    </div>
                                    <div class="icon">
                                        <!-- <img :src="content.image" class="card-img" style="width: 2em;"/> -->
                                    </div>
                                    <div class="small-box-footer mb-0 p-2">
                                        <a href="javascript:void(0)" class="text-success mr-2" @click="filterContentDetails(content.id)">More<i class="fas fa-arrow-circle-right"></i></a>
                                        <a href="javascript:void(0)" class="text-light" @click="filterContent(content.id)">Edit<i class="fas fa-arrow-circle-right"></i></a>
                                        <a href="javascript:void(0)" class="p-2 text-danger" @click="handleDeleteContent(content.id)">Delete</a>
                                    </div>
                                </div>
                                

                            </div>
                        </div>

                    </div>

                    <!-- Create Content -->
                    <div v-else >

                        <div v-if="Content === null">
                            <form method="POST" @submit.prevent="handleNewContent">
                                <h3 class="text-start">
                                    <a href="javascript:void(0)" class="text-dark" @click="loadContentView()"><i class="mdi mdi-chevron-left"></i></a> 
                                    Create a new content
                                </h3>
                                <div class="pb-5">
                
                                    <div class="col-xl-6 col-md-12 my-4">
                                        <!-- XHR Response -->
                                        <!-- <div class="form-group text-start">
                                            <input type="file" name="file_holder" ref="image" class="d-none" id="profile_image" @change.prevent="changeImage" />
                                            <label for="profile_image" style="cursor: pointer;"><img src="@/assets/images/profile.png" class="img-fluid" style="width: 5em;"/></label>
                                        </div> -->
                                            
                                        <div class="form-group text-start">
                                            <label for="">Title</label>
                                            <input type="text" v-model="form.title" class="form-control remove-radius" placeholder="Enter title" required/>
                                        </div>
                                            
                                        <div class="form-group text-start">
                                            <label for="">Content I(optional)</label>
                                            <textarea v-model="form.first_content" class="form-control remove-radius" rows="10" placeholder="Enter first content"></textarea>
                                        </div>
                                            
                                        <div class="form-group text-start">
                                            <label for="">Content II(optional)</label>
                                            <textarea v-model="form.second_content" class="form-control remove-radius" rows="10" placeholder="Enter second content"></textarea>
                                        </div>

                                        <div class="form-group text-start">
                                            <label for="">Page</label>
                                            <select v-model="form.page" class="custom-select remove-radius" placeholder="Enter venue" required>
                                                <option v-for="page in pages" v-bind:key="page.id" :value="page.slug">{{page.pathname}}</option>
                                            </select>
                                        </div>
                
                                    </div>

                                    <div class="col-xl-6 col-md-12 mb-2 text-start">
                                        <button class="btn btn-success" type="submit"><i class="fa fa-save"></i> Save</button>
                                    </div>

                                </div>

                            </form>
                        </div>
                        <div v-else >

                            <div v-if="ContentDetailView === false">
                                <form method="POST" @submit.prevent="handleUpdatedContent">
                                    <h3 class="text-start">
                                        <a href="javascript:void(0)" class="text-dark" @click="loadContentView()"><i class="mdi mdi-chevron-left"></i></a> 
                                        Update content
                                    </h3>
                                    <div class="pb-5">
                    
                                        <div class="col-xl-6 col-md-12 my-4">
                                            <!-- XHR Response -->
                                            <div class="form-group text-start">
                                                <input type="file" name="file_holder" ref="image" class="d-none" id="profile_image" @change.prevent="changeImage" />
                                                <label for="profile_image" style="cursor: pointer;" v-if="Content.banner === null || Content.banner === ''" ><img src="@/assets/images/image.png" class="img-fluid" style="width: 5em;" /></label>
                                                <label for="profile_image" style="cursor: pointer;" v-else ><img :src="Content.banner" class="img-fluid" style="width: 5em;"/></label>
                                            </div>
                                                
                                            <div class="form-group text-start">
                                                <label for="">Title</label>
                                                <input type="text" v-model="Content.title" class="form-control remove-radius" placeholder="Enter title" required/>
                                            </div>
                                                
                                            <div class="form-group text-start">
                                                <label for="">Content I(optional)</label>
                                                <textarea v-model="Content.first_content" class="form-control remove-radius" rows="10" placeholder="Enter first content"></textarea>
                                            </div>
                                                
                                            <div class="form-group text-start">
                                                <label for="">Content II(optional)</label>
                                                <textarea v-model="Content.second_content" class="form-control remove-radius" rows="10" placeholder="Enter second content"></textarea>
                                            </div>

                                            <div class="form-group text-start">
                                                <label for="">Page</label>
                                                <select v-model="Content.page" class="custom-select remove-radius" placeholder="Enter venue" required>
                                                    <option v-for="page in pages" v-bind:key="page.id" :value="page.slug">{{page.pathname}}</option>
                                                </select>
                                            </div>
                    
                                        </div>

                                        <div class="col-xl-6 col-md-12 mb-2 text-start">
                                            <button class="btn btn-success" type="submit"><i class="fa fa-save"></i> Save</button>
                                        </div>

                                    </div>

                                </form>
                            </div>

                            <div v-else >
                                
                                <!-- New content detail -->
                                <div v-if="NewContentDetail === true">
                                    <form method="POST" @submit.prevent="handleNewContentDetail">
                                        <h3 class="text-start">
                                            <a href="javascript:void(0)" class="text-dark" @click="loadContentDetailView()"><i class="mdi mdi-chevron-left"></i></a> 
                                            Create a new content detail ({{Content.title}})
                                        </h3>
                                        <div class="pb-5">
                        
                                            <div class="col-xl-6 col-md-12 my-4">
                                                <!-- XHR Response -->
                                                <!-- <div class="form-group text-start">
                                                    <input type="file" name="file_holder" ref="image" class="d-none" id="profile_image" @change.prevent="changeImage" />
                                                    <label for="profile_image" style="cursor: pointer;"><img src="@/assets/images/profile.png" class="img-fluid" style="width: 5em;"/></label>
                                                </div> -->

                                                <div class="form-group text-start">
                                                    <label for="">Info</label>
                                                    <input type="text" v-model="content_detail_form.info" class="form-control remove-radius" placeholder="Enter content info"/>
                                                </div>
                                                    
                                                <div class="form-group text-start">
                                                    <label for="">Caption title(optional)</label>
                                                    <input type="text" v-model="content_detail_form.caption_title" class="form-control remove-radius" placeholder="Enter caption title"/>
                                                </div>
                                                    
                                                <div class="form-group text-start">
                                                    <label for="">Caption body(optional)</label>
                                                    <input type="text" v-model="content_detail_form.caption_body" class="form-control remove-radius" placeholder="Enter caption body"/>
                                                </div>
                                                    
                                                <div class="form-group text-start">
                                                    <label for="">Link(optional)</label>
                                                    <input type="text" v-model="content_detail_form.link" class="form-control remove-radius" placeholder="Enter link"/>
                                                </div>
                                                    
                                                <div class="form-group text-start">
                                                    <label for="">Link text(optional)</label>
                                                    <input type="text" v-model="content_detail_form.link_text" class="form-control remove-radius" placeholder="Enter link text"/>
                                                </div>

                                                <div class="form-group text-start">
                                                    <label for="">Slide</label>
                                                    <select v-model="content_detail_form.slide" class="custom-select remove-radius" placeholder="Enter slide" required>
                                                        <option value="1">True</option>
                                                        <option value="0">False</option>
                                                    </select>
                                                </div>

                                                <div class="form-group text-start">
                                                    <label for="">Visibility</label>
                                                    <select v-model="content_detail_form.visibility" class="custom-select remove-radius" placeholder="Enter slide" required>
                                                        <option value="1">True</option>
                                                        <option value="0">False</option>
                                                    </select>
                                                </div>

                                                <div class="form-group text-start">
                                                    <label for="">Icon(optional)</label>
                                                    <input type="text" v-model="content_detail_form.icon" class="form-control remove-radius" placeholder="Enter icon"/>
                                                </div>

                                                <div class="form-group text-start">
                                                    <label for="">Icon link(optional)</label>
                                                    <input type="text" v-model="content_detail_form.icon_link" class="form-control remove-radius" placeholder="Enter icon link"/>
                                                </div>
                           
                                            </div>

                                            <div class="col-xl-6 col-md-12 mb-2 text-start">
                                                <button class="btn btn-success" type="submit"><i class="fa fa-save"></i> Save</button>
                                            </div>

                                        </div>

                                    </form>
                                </div>

                                <div v-else >

                                    <!-- All Content details -->
                                    <div v-if="Content_detail === null">
                                        
                                        <h3 class="text-start">
                                            <a href="javascript:void(0)" class="text-dark" @click="loadContentView()"><i class="mdi mdi-chevron-left"></i></a> {{Content.title}}<sup><a href="javascript:void(0)" class="text-dark" @click.prevent="newContentDetailView" title="Create"><i class="mdi mdi-plus-circle"></i></a></sup>
                                        </h3>

                                        <div class="text-start" v-if="content_details.length === 0">
                                            <b>No content details yet</b>
                                        </div>

                                        <div class="row no-gutters pb-5" v-else >
                                            <div class="col-xl-3 col-sm-6 col-6 h-100 pr-2" v-for="content_detail in content_details" v-bind:key="content_detail.id">
                                                <!-- small box -->
                                                <div class="small-box bg-dark h-100 pt-4">
                                                    <div class="inner">
                                                        <div v-if="content_detail.image === null">
                                                            <img src="@/assets/images/image.png" class="img-fluid" style="width: 8em; height: 8em; object-fit: contain;" />
                                                        </div>
                                                        <div v-else >
                                                            <div v-if="content_detail.image.split('.')[content_detail.image.split('.').length-1] === 'mp4'">
                                                                <video src="@/assets/images/bv4.mp4" v-if="content_detail.image === null" class="d-block w-100 img-resize mb-2" height="120" autoplay></video>
                                                                <video :src="content_detail.image" v-else class="d-block w-100 img-resize mb-2" height="120" autoplay></video>
                                                            </div>
                                                            <div v-else >
                                                                <img :src="content_detail.image" class="img-fluid" style="width: 8em; height: 8em; object-fit: contain;" />
                                                            </div>
                                                        </div>
                                                        <!-- <i class="mdi mdi-plus-circle" v-if="content.image === null"></i> -->
                                                        <h5 class="text-truncate" :title="content_detail.info">{{content_detail.info}}</h5>
                                                    </div>
                                                    <div class="icon">
                                                        <!-- <img :src="content.image" class="card-img" style="width: 2em;"/> -->
                                                    </div>
                                                    <div class="small-box-footer mb-0 p-2">
                                                        <a href="javascript:void(0)" class="text-light" @click="filterContentDetail(content_detail.id)">Edit<i class="fas fa-arrow-circle-right"></i></a>
                                                        <a href="javascript:void(0)" class="p-2 text-danger" @click="handleDeleteContentDetail(content_detail.id)">Delete</a>
                                                    </div>
                                                </div>
                                                

                                            </div>
                                        </div>

                                    </div>

                                    <!-- Update content detail -->
                                    <div v-else >
                                        <h3 class="text-start">
                                            <a href="javascript:void(0)" class="text-dark" @click="loadContentDetailView"><i class="mdi mdi-chevron-left"></i></a> {{`${Content_detail.info} (${Content.title})`}}
                                        </h3>

                                        <form method="POST" @submit.prevent="handleUpdatedContentDetail">
                                            <h5 class="text-start">
                                                Update content detail
                                            </h5>
                                            <div class="pb-5">
                            
                                                <div class="col-xl-6 col-md-12 text-start my-4">
                                                    
                                                    <div class="form-group mr-3">
                                                        <input type="file" name="file_holder" ref="content_image" class="d-none" id="profile_image" @change.prevent="changeContentImage" />
                                                        <label for="profile_image" style="cursor: pointer;" v-if="Content_detail.image === null"><img src="@/assets/images/image.png" class="img-fluid" style="width: 5em;"/></label>
                                                        <label for="profile_image" style="cursor: pointer;" v-else >
                                                            <div v-if="Content_detail.image.split('.')[Content_detail.image.split('.').length-1] === 'mp4'">
                                                                <video src="@/assets/images/bv4.mp4" v-if="Content_detail.image === null" class="d-block w-35 img-resize mb-2" height="120" autoplay></video>
                                                                <video :src="Content_detail.image" v-else class="d-block w-35 img-resize mb-2" height="120" autoplay></video>
                                                            </div>
                                                            <div v-else >
                                                                <img :src="Content_detail.image" class="img-fluid" style="width: 8em; height: 8em; object-fit: contain;" />
                                                            </div>
                                                            <!-- <img :src="Content_detail.image" class="img-fluid" style="width: 5em;"/> -->
                                                        </label>
                                                    </div>

                                                    <div class="form-group text-start">
                                                        <label for="">Info</label>
                                                        <input type="text" v-model="Content_detail.info" class="form-control remove-radius" placeholder="Enter content info"/>
                                                    </div>
                                                        
                                                    <div class="form-group text-start">
                                                        <label for="">Caption title(optional)</label>
                                                        <input type="text" v-model="Content_detail.caption_title" class="form-control remove-radius" placeholder="Enter caption title"/>
                                                    </div>
                                                        
                                                    <div class="form-group text-start">
                                                        <label for="">Caption body(optional)</label>
                                                        <input type="text" v-model="Content_detail.caption_body" class="form-control remove-radius" placeholder="Enter caption body"/>
                                                    </div>
                                                        
                                                    <div class="form-group text-start">
                                                        <label for="">Link(optional)</label>
                                                        <input type="text" v-model="Content_detail.link" class="form-control remove-radius" placeholder="Enter link"/>
                                                    </div>
                                                        
                                                    <div class="form-group text-start">
                                                        <label for="">Link text(optional)</label>
                                                        <input type="text" v-model="Content_detail.link_text" class="form-control remove-radius" placeholder="Enter link text"/>
                                                    </div>

                                                    <div class="form-group text-start">
                                                        <label for="">Slide</label>
                                                        <select v-model="Content_detail.slide" class="custom-select remove-radius" placeholder="Enter slide" required>
                                                            <option value="1">True</option>
                                                            <option value="0">False</option>
                                                        </select>
                                                    </div>

                                                    <div class="form-group text-start">
                                                        <label for="">Visibility</label>
                                                        <select v-model="Content_detail.visibility" class="custom-select remove-radius" placeholder="Enter slide" required>
                                                            <option value="1">True</option>
                                                            <option value="0">False</option>
                                                        </select>
                                                    </div>

                                                    <div class="form-group text-start">
                                                        <label for="">Icon(optional)</label>
                                                        <input type="text" v-model="Content_detail.icon" class="form-control remove-radius" placeholder="Enter icon"/>
                                                    </div>

                                                    <div class="form-group text-start">
                                                        <label for="">Icon link(optional)</label>
                                                        <input type="text" v-model="Content_detail.icon_link" class="form-control remove-radius" placeholder="Enter icon link"/>
                                                    </div>
                            
                                                </div>

                                                <div class="col-xl-6 col-md-12 mb-2 text-start">
                                                    <button class="btn btn-success" type="submit"><i class="fa fa-save"></i> Save</button>
                                                </div>

                                            </div>

                                        </form>

                                    </div>

                                </div>
                                
                            </div>

                        </div>
                    
                    </div>

                </div>

            </section>
            <!-- /.content -->

        </div>

        <ConsoleFooter />
    </div>
</template>


<script>
import ConsoleHeader from "./layouts/ConsoleHeader.vue";
import ConsoleFooter from "./layouts/ConsoleFooter.vue";

import {mapActions, mapGetters} from 'vuex';

import Data from '@/lib/Data';

import axios from 'axios';
import AuthenticationService from '@/services/AuthenticationService';

export default {
    name: "ConsoleSecurity",
    computed: mapGetters(['contents', 'content_details']),
    data(){
        return {
            // NewContentDetailView
            NewContentView: false,
            Content: null,
            Content_detail: null,
            ContentDetailView: false,
            NewContentDetail: false,
            pages: Data.pages,
            form: {
                title: null,
                banner: null,
                first_content: null,
                second_content: null,
                page: 1,
            },
            content_detail_form: {
                content_id: null,
                info: null,
                image: null,
                caption_title: null,
                caption_body: null,
                link: null,
                link_text: null,
                slide: 1,
                visibility: 1,
                icon: null,
                icon_link: null
            }
        }
    },
    components: {
        ConsoleHeader,
        ConsoleFooter
    },
    methods: {
        ...mapActions(['fetchContents', 'deleteContent', 'createContent', 'updateContent', 'fetchContentDetails', 'createContentDetail', 'updateContentDetail', 'deleteContentDetail']),
        async handleNewContent(){
            await this.createContent(this.form);
            this.NewContentView = false;
            
        },
        async newContentView(){
            this.NewContentView = true;
            this.Content = null;
        },
        async newContentDetailView(){
            this.NewContentDetail = true;
        },
        async loadContentView(){
            this.NewContentView = false
            this.Content = null;
        },
        async loadContentDetailView(){
            this.NewContentDetail = false;
            this.Content_detail = null;
        },
        async filterContent(id){
            this.Content = this.contents.filter(content => content.id === id);
            if (this.Content.length > 0) {
                this.Content = this.Content[0];
                this.NewContentView = null;
                this.ContentDetailView = false;
            }
        },
        async changeImage(){
            this.image = this.$refs.image.files[0];
            let formData = new FormData();
            formData.append('upload_preset', 'webpages');
            formData.append('tags', 'unsigned upload, webpages');
            formData.append('file', this.image);
            

            let response = await axios.post(`https://api.cloudinary.com/v1_1/cacsafpi/upload`, formData);

            if (response.data.secure_url !== null) {
                let old_image = this.Content.banner;
                this.Content.banner = response.data.secure_url;
                await this.updateContent({id: this.Content.id, content: this.Content});

                switch (old_image) {
                    case '':
                        break;
                    case null:
                        break;
                    default:
                        await AuthenticationService.removeImage({old_image});
                        break;
                }

            }
        },
        async changeContentImage(){
            this.image = this.$refs.content_image.files[0];
            let formData = new FormData();
            formData.append('upload_preset', 'webpages');
            formData.append('tags', 'unsigned upload, webpages');
            formData.append('file', this.image);
            

            let response = await axios.post(`https://api.cloudinary.com/v1_1/cacsafpi/upload`, formData);

            if (response.data.secure_url !== null) {
                let old_image = this.Content_detail.image;
                this.Content_detail.image = response.data.secure_url;
                await this.updateContentDetail({id: this.Content_detail.id, content_detail: this.Content_detail});

                switch (old_image) {
                    case '':
                        break;
                    case null:
                        break;
                    default:
                        await AuthenticationService.removeImage({old_image});
                        break;
                }

            }
        },
        async handleDeleteContent(id){
            let content = this.contents.filter(content => content.id === id);
            // console.log(content);
            if (content.length > 0) {
                if (content[0].banner !== null) {
                    const response = await AuthenticationService.removeImage({old_image: content[0].banner});
                    if(response.data.error === 0){
                        await this.deleteContent(id);
                    }
                }
                else{
                    await this.deleteContent(id);
                }
            }
        },
        async handleUpdatedContent(){
            if (this.Content !== null) {
                await this.updateContent({id: this.Content.id, content: this.Content});
                this.NewContentView = false;
            }
        },
        async handleUpdatedContentDetail(){
            if (this.Content_detail !== null) {
                await this.updateContentDetail({id: this.Content_detail.id, content_detail: this.Content_detail});
                // this.NewContentDetail = false;
                this.Content_detail = null;
            }
        },

        async filterContentDetails(id){
            this.Content = this.contents.filter(content => content.id === id);
            if (this.Content.length > 0) {
                this.Content = this.Content[0];
                this.NewContentView = null;
                this.ContentDetailView = true;
                this.NewContentDetail = false;
                await this.fetchContentDetails(id);
            }
            
        },
        async handleNewContentDetail(){
            this.content_detail_form.content_id = this.Content.id;
            await this.createContentDetail(this.content_detail_form);
            this.NewContentDetail = false;
        },
        async handleDeleteContentDetail(id) {
            let content_detail = this.content_details.filter(content_detail => content_detail.id === id);
            // console.log(content_detail);
            if (content_detail.length > 0) {
                if (content_detail[0].image !== null) {
                    const response = await AuthenticationService.removeImage({old_image: content_detail[0].image});
                    if(response.data.error === 0){
                        await this.deleteContentDetail(id);
                    }
                }
                else{
                    await this.deleteContentDetail(id);
                }
            }
        },
        async filterContentDetail(id){
            // Content_detail
            this.Content_detail = this.content_details.filter(content_detail => content_detail.id === id);
            if (this.Content_detail.length > 0) {
                this.Content_detail = this.Content_detail[0];
                // this.NewContentView = null;
                // this.ContentDetailView = true;
                // this.NewContentDetail = false;
                // await this.fetchContentDetails(id);
            }
        }
    },
    created(){
        this.fetchContents();
    }

}
</script>

<style scoped>
    
</style>